<script lang="js">
export default {
  data () {
    return {
      drawer: false, // 
      LastClick: '', // 
      RuleIndex: 0,
      RulePreviousBtnDisabled: false,
      RuleNextBtnDisabled: false,
      dialogVisible: false,
      options: { // 
        responsive: true,
        plugins: { // chartjs-plugin-datalabels
          datalabels: {
            formatter: (value, ctx) => { // 
              const LEGEND_PERCENTAGE = value + '%' // '%'
              return LEGEND_PERCENTAGE
              // '
              // let sum = 0
              // const dataArr = ctx.chart.data.datasets[0].data
              // dataArr.map(data => {
              //   sum += data
              //   return 0
              // })
              // const percentage = (value * 100 / sum).toFixed(2) + '%'
            },
            color: 'black', // labels
            align: 'start',
            textAlign: 'center', // labels
            font: {
              weight: 'bold',
              size: 18
            },
            offset: -40
          }
        }
      },
      Rules: [
        '', '', '', '', '', '', '', '', ''
      ]
    }
  },
  metaInfo: {
    title: '',
    meta: [
      {
        charset: 'utf-8'
      },
      {
        name: 'keywords', content: '  50000    nkust ic'
      },
      {
        name: 'description', content: ''
      }
    ]
  },
  methods: {
    ScrollProcess (section) {
      this.LastClick = section
      this.drawer = false
    },
    // Vue
    // section idLastClick(drawer)
    // (drawer)element ui DrawerClosed eventsection id(LastClick)documentmethod:scrollIntoView
    Scroll (section) {
      // sectionidEL
      // documentmethod:scrollIntoView
      const EL = document.getElementById(section)
      if (EL) {
        EL.scrollIntoView({
          behavior: 'smooth'
        })
      }
    },
    DrawerClosed () {
      this.Scroll(this.LastClick)
      // (drawer)element ui DrawerClosed eventsection id(LastClick)documentmethod:scrollIntoView
    },
    openUrl (url) {
      window.open(url, '_blank')
    },
    RuleOpen (index) {
      if (index === 0) {
        this.RulePreviousBtnDisabled = true
        this.RuleNextBtnDisabled = false
      } else if (index === this.Rules.length - 1) {
        this.RulePreviousBtnDisabled = false
        this.RuleNextBtnDisabled = true
      } else {
        this.RulePreviousBtnDisabled = false
        this.RuleNextBtnDisabled = false
      }
      this.dialogVisible = true
      this.RuleIndex = index
      document.body.style.height = '100%'
      document.body.style.overflowY = 'hidden'
    },
    RuleClose () {
      this.dialogVisible = false
      document.body.style.height = ''
      document.body.style.overflowY = ''
    },
    RuleMove (action) {
      switch (action) {
        case 'previous':
          this.RuleIndex--
          break
        case 'next':
          this.RuleIndex++
          break
      }
      if (this.RuleIndex === 0) {
        this.RulePreviousBtnDisabled = true
        this.RuleNextBtnDisabled = false
      } else if (this.RuleIndex === this.Rules.length - 1) {
        this.RulePreviousBtnDisabled = false
        this.RuleNextBtnDisabled = true
      } else {
        this.RulePreviousBtnDisabled = false
        this.RuleNextBtnDisabled = false
      }
    }
  },
  mounted () {
    const self = this// thisvue
    // callbackvue
    // hookvar self = thisvuecallbackself
    window.addEventListener('scroll', function () {
      switch (Math.round(window.scrollY / parseInt(document.body.clientHeight / 8))) {
        case (0): {
          self.$router.push('').catch(() => {})
          break
        }
        case (1): {
          self.$router.push('#Info').catch(() => {})
          break
        }
